<template>
  <div id="product">
    <!-- 轮播组件 -->
    <Banner :item="banner[0]"></Banner>
    <!-- 商品价格等信息 -->
    <GoodsInfo :item="info"></GoodsInfo>
    
  </div>
</template>
<script>
// 轮播组件
import Banner from "./Banner";

// 商品信息
import GoodsInfo from "./GoodsInfo";
export default {
  components: {
    Banner,
    GoodsInfo,
    //  Referral,
  },
  data() {
    return {
      //   商品详情轮播图
      banner: [],
      // 商品信息
      info: null,
      // 商品规格
      standard: null,
      // 商品规格综合选
      strength: null,
    };
  },
  props: ["id"],
  created() {
    this.getGoods();
  },
  watch:{
    // id(){
      // this.getGoods();
    // }
  },
  methods: {
    //商品规格层的弹入与弹出
    showPopup() {
      // 父组件使用ref给子组件绑定，触发子组件的事件或者属性
      this.$refs.standard.showPopup();
    },
    getGoods() {
      this.axios
        .get("http://47.115.51.185/api/product/detail/" + this.id)
        .then((res) => {
          let data = res.data.data;
          console.log(data);
          // 轮播图
          this.banner = data.storeInfo.slider_image;
          // console.log(this.banner);
          // 商品信息
          this.info = data.storeInfo;
        });
    },
  },
};
</script>
<style lang="less">
#product {
  width: 100vw;
  box-sizing: border-box;
  background-color: #f5f5f5;
  // 商品规格左标题
  .spec {
    text-align: left;
    color: #82848f;
    font-size: 0.26rem;
  }
  // 商品选择
  .spec12 {
    margin-top: 0.2rem;
  }
}
</style>