<template>
  <div class="referral">
    <div class="title">
      <img
        src="data:image/png;base64,"
        alt=""
      />
      <span>优品推荐</span>
      <img
        src="data:image/png;base64,"
        alt=""
      />
    </div>
    <div class="referral_banner" v-if="list">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(i, index) in list.length / 6" :key="index">
          <div class="googs_lists">
            <ul class="goods_uls">
              <router-link tag="li" :to="`/detail/${d.id}`"
                v-for="(d, ids) in list.slice(index * 6, (index + 1) * 6)"
                :key="ids"
              >
                <div class="list_img">
                  <img :src="d.image" />
                </div>
                <div class="van-ellipsis">{{ d.store_name }}</div>
                <p>￥{{ d.price }}</p>
              </router-link>
            </ul>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: null,
      
    };
  },
  props: ["id"],
  created() {
    this.axios
      .get("http://47.115.51.185/api/product/detail/" + this.id)
      .then((res) => {
        this.list = res.data.data.good_list;
        // console.log(this.list);
        
      });
  },
};
</script>
<style lang="less">
.referral {
  background-color: #fff;
  margin-top: 0.2rem;
//   padding-bottom: 0.5rem;
  // 头部
  .title {
    height: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 0.4rem;
      height: 0.4rem;
    }
    span {
      font-size: 0.35rem;
      background-image: linear-gradient(90deg, #f57a37 0, #f21b07);
      -webkit-text-fill-color: transparent;
      // 渐变字体
      -webkit-background-clip: text;
      margin: 0 0.2rem;
    }
  }
  //   推荐轮播
  .referral_banner {
      padding-bottom: 0.5rem;
      position: relative;
    .googs_lists {
      width: 100vw;
      box-sizing: border-box;
      .goods_uls {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        li {
          width: 3rem;
          margin-bottom: 0.2rem;
          .list_img {
            height: 3rem;
            overflow: hidden;
            img {
              width: 100%;
              height: auto;
            }
          }
          div:nth-child(2) {
            color: #282828;
            margin-top: 0.12rem;
            font-size: 0.26rem;
          }
          p {
            color: #fc4141 !important;
            font-size: 0.26rem;
            text-align: left;
          }
        }
      }
    }
    .my-swipe{
        overflow: initial;
        .van-swipe__indicators{
            bottom: -0.2rem;
            .van-swipe__indicator{
                width: 0.3rem;
                height: 0.3rem;
                background-color: #999;
                // #e93323
            }
            .van-swipe__indicator--active{
                background-color: #e93323 !important;
            }
        }
    }
  }
}
</style>