<template>
  <!-- 商品规格 -->
  <div id="standard">
    <!-- 商品规格 -->
    <van-popup round v-model="show" closeable position="bottom">
      <div class="standard_box">
        <!-- 顶部 -->
        <!-- <DeTop :item="strength[suks]" :yname="info" v-if="suks"></DeTop> -->
        <template v-if="suks">
          <!-- <template v-if=""> -->
          <DeTop :item="strength[suks]" :yname="info" v-if="suks"></DeTop>
        </template>
        <template v-if="standard">
          <div v-if="!standard.length">
            <!-- 电器等名义规格的商品 -->
            <DeTop :item="strength[suks]" :yname="info" :flag="true"></DeTop>
          </div>
        </template>
        <div class="gg" v-if="suk">
          <van-radio-group
            v-model="suk[index]"
            v-for="(i, index) in standard"
            :key="index"
          >
            <p>{{ i.attr_name }}</p>
            <div class="iptj">
              <div class="iptw" v-for="d in i.attr_value" :key="d.attr">
                <!-- {{ d.attr }} -->
                <van-radio :name="d.attr">{{ d.attr }}</van-radio>
              </div>
            </div>
          </van-radio-group>
          <div class="st_num" v-if="strength">
            <p>数量</p>
            <!-- :max="spec?strength[suks].stock:info.stock" -->
            <van-stepper
              v-model="value"
              integer
              :max="spec ? strength[suks].stock : info.stock"
              input-width="1.5rem"
              button-size="1rem"
            />
          </div>
        </div>
      </div>
    </van-popup>
    <!-- 底部购物车 -->
    <van-goods-action v-if="info">
      <van-goods-action-icon icon="chat-o" text="客服" />
      <van-goods-action-icon
        :icon="userCollect ? 'like' : 'like-o'"
        :class="userCollect ? 'red' : ''"
        text="收藏"
        @click="hm"
      />
      <van-goods-action-icon icon="cart-o" text="购物车" :badge="cart_count" />
      <!-- :text="strength[suks].stock?'立即购买':'已售罄' -->
      <van-goods-action-button type="warning" text="加入购物车" @click="add" />
      <!-- spec?strength[suks].stock:info.stock -->
      <van-goods-action-button
        type="danger"
        :class="strength[suks].stock ? '' : 'gray'"
        :text="strength[suks].stock ? '立即购买' : '已售罄'"
        @click="buy"
        v-if="spec"
      />
      <van-goods-action-button
        type="danger"
        :class="info.stock ? '' : 'gray'"
        :text="info.stock ? '立即购买' : '已售罄'"
        @click="buy"
        v-else
      />
    </van-goods-action>
  </div>
</template>
  <script>
// 购物规格顶部
import DeTop from "./Top.vue";
export default {
  components: {
    DeTop,
  },
  data() {
    return {
      //商品规格层的弹入与弹出
      show: false,
      // 商品规格
      standard: null,
      // 商品规格综合选
      strength: null,
      sku: 1,
      suk: [],
      // 商品信息
      info: null,
      // 计步器,商品数量
      value: 1,
      // 添加模式为false表示点击是为了弹出商品规格层，为true表示添加购物车
      add_mode: false,
      //  购买模式。false为选商品规格。true为立即购买
      buy_mode: false,
      // 底部购买栏，购物车数量
      cart_count: "",
      // 点击爱心判断是否已收藏，true表示已收藏，false表示取消收藏或者未收藏
      hm_flag: false,
      // 获取商品信息里面的用户是否收藏
      userCollect: null,
      // 是否存在规格，电器不存在规格,默认存在
      spec: true,
    };
  },
  methods: {
    //商品规格层的弹入与弹出
    showPopup() {
      this.show = true;
    },
    // 加入购物车按钮点击
    add() {
      if (!this.add_mode) {
        this.showPopup();
      } else {
        this.add_cart().then(() => {
          this.$toast("添加购物车成功");
          this.show = false;
        });
      }
    },
    // 立即购买按钮点击
    buy() {
      if (this.spec) {
        // 存在规格，且已售罄
        if (!this.strength[this.suks].stock) {
          return false;
        }
      } else {
        // 不存在规格，且已售罄
        if (!this.info.stock) {
          return false;
        }
      }
      if (!this.buy_mode) {
        this.showPopup();
      } else {
        this.add_cart().then((res) => {
          // console.log(res);
          let cartId = res.data.data.cartId;
          // console.log(cartId);
          this.$router.push("/order/submit/" + cartId);
        });
      }
    },
    // 收藏
    hm() {
      // 点击前为收藏状态，则此次点击为取消收藏
      if (this.userCollect) {
        this.userCollect = false;
        this.axios
          .post("/api/collect/del", {
            category: "product",
            id: this.id,
          })
          .then((res) => {
            console.log(res);
            console.log("取消收藏成功");
          });
      } else {
        this.userCollect = true;
        // 收藏
        this.axios
          .post("/api/collect/add", {
            category: "product",
            id: this.id,
          })
          .then((res) => {
            console.log(res);
            console.log("收藏成功");
          });
      }
    },
    // 加入购物车执行的方法
    add_cart() {
      // 商品数量;
      let num = this.value;
      let id = this.id;
      let unique = "";
      // console.log(Array.isArray(this.strength));
      // this.strength是一个数组表示不存在商品规格选项，是一个对象就是存在商品规格
      if (!Array.isArray(this.strength)) {
        unique = this.strength[this.suks].unique;
      }
      // 发起加入购物车的请求;
      return this.axios.post("/api/cart/add", {
        cartNum: num,
        new: 0,
        productId: id,
        uniqueId: unique,
      });
      //
    },

    // 初始化请求
    getGoods() {
      this.axios
        .get("http://47.115.51.185/api/product/detail/" + this.id)
        .then((res) => {
          let data = res.data.data;
          // console.log(data);
          // 商品信息
          this.info = data.storeInfo;
          // console.log(this.info);
          // 商品规格
          this.standard = data.productAttr;
          // console.log("商品规格",this.standard);
          if (this.standard.length == 0) {
            // console.log("aaa");
            this.standard = [];
          }
          this.suk = this.standard.map((d) => {
            return d.attr_value[0].attr;
          });
          // console.log(this.standard);
          // 商品,规格综合
          this.strength = data.productValue;
          if (Array.isArray(this.strength)) {
            // 表示不存在规格
            this.spec = false;
          } else {
            // 存在规格
            this.spec = true;
          }
          // console.log("综合=>",this.strength);
          // 用户是否收藏
          this.userCollect = data.storeInfo.userCollect;
          // console.log(this.userCollect);
        });
      // 底部购物车数量
      this.axios.get("api/cart/count?numType=0").then((res) => {
        // console.log(res);
        this.cart_count = res.data.data.count;
      });
    },
  },
  watch: {
    // 为了监听商品规格层的显示与隐藏，当商品规格层隐藏之后，加入购物车和立即购买的按钮为了下次点击仍能是弹出层的效果，当商品规格层隐藏之后，计数器减一
    show(news) {
      if (!news) {
        // 隐藏
        this.add_mode = false;
        this.buy_mode = false;
      } else {
        // 显示
        this.add_mode = true;
        this.buy_mode = true;
      }
    },
    id(){
      this.getGoods();
    }
  },
  // standard 商品规格
  // strength商品规格综合
  props: ["id"],
  created() {
    this.getGoods();
  },
  computed: {
    suks() {
      // console.log(this.suk.join(","));
      this.$emit("changGg", this.suk.join(","));
      // console.log(this.strength[this.suks].stock);
      // console.log(this.strength);
      return this.suk.join(",");
    },
  },
  mounted() {},
  updated() {
    // console.log(this.suks.length);
    // console.log(this.strength[this.suks]);
  },
};
</script>
  <style lang="less">
#standard {
  .van-goods-action {
    z-index: 99999;
  }
  .standard_box {
    width: 100vw;
    box-sizing: border-box;
    padding: 0 0.3rem;
    padding-top: 0.5rem;
    padding-bottom: 1.33333rem;
    overflow: hidden;
    .gg {
      .van-radio-group {
        margin-top: 0.36rem;
        p {
          color: #999;
          font-size: 0.35rem;
          text-align: left;
        }
        .iptj {
          padding: 0.3rem 0 0.16rem;
          display: flex;
          flex-wrap: wrap;
          .iptw {
            width: 1.4rem;
            height: 0.8rem;
            font-size: 0.26rem;
            position: relative;
            margin: 0.14rem 0.4rem 0 0;
            padding: 0.07rem 0.33rem;
            border-radius: 0.06rem;
            box-sizing: border-box;
            border: 1px solid #bbb;
            position: relative;
            display: flex;
            //单选框
            .van-radio {
              width: 100%;
              height: 100%;
              font-size: 2rem;
              z-index: 16;
              .van-radio__icon--checked + span {
                background-color: #ff3700;
                color: #fff;
              }
              .van-radio__icon--round {
                i {
                  display: none;
                }
              }
              span {
                margin: 0 0;
                width: 1.5rem;
                line-height: 0.8rem;
                height: 0.8rem;
                font-size: 0.26rem;
                border-radius: 0.06rem;
                position: absolute;
                top: 0;
                left: 0;
                color: #282828;
              }
            }
          }
        }
      }
      // 计步器
      .st_num {
        // margin-top: 0.4rem;
        padding-bottom: 0.5rem;
        p {
          color: #999;
          font-size: 0.35rem;
          text-align: left;
        }
        .van-stepper {
          margin-top: 0.3rem;
          text-align: left;
        }
      }
    }
  }
  .red {
    .van-goods-action-icon__icon {
      color: #e93323;
    }
  }
  .gray {
    background: #bbb !important;
    color: #fff !important;
  }
}
</style>