<template>
  <div id="detail">
    <!-- 滚动导航 -->
    <van-tabs v-model="active" scrollspy sticky ref="s">
      <van-tab title="商品">
        <Goods :id="id"></Goods>
      </van-tab>
      <!-- 商品规格选择 -->
      <van-cell
        is-link
        :border="false"
        title-class="spec"
        @click="showPopup"
        class="spec12"
        v-if="flag"
      >
        <!-- {{pickOn}} -->
        <template #title>
          <span class="custom-title" v-if="g_name">已选择：{{ g_name }}</span>
          <van-tag type="danger"></van-tag>
        </template>
      </van-cell>
      <van-tab title="评价" ref="pj">
        <!-- 用户评价 -->
        <van-cell title="用户评价" class="praise">
          <!-- 使用 right-icon 插槽来自定义右侧图标 -->
          <template #right-icon>
            <span class="hp">0%</span>好评率
            <van-icon name="arrow" />
          </template>
        </van-cell>
      </van-tab>
      <van-tab title="推荐">
        <!-- 优品推荐 -->
        <Referral :id="id" v-if="id"></Referral>
      </van-tab>
      <van-tab title="详情">
        <!-- 产品介绍 -->
        <div class="products" v-if="des">产品介绍</div>
        <div class="param" v-html="des"></div>
        <!-- 商品规格选择 -->
        <van-cell
          is-link
          :border="false"
          title-class="spec"
          @click="showPopup"
          class="spec12"
        >
          <template #title>
            <span class="custom-title">已选择：</span>
            <van-tag type="danger"></van-tag>
          </template>
        </van-cell>
        <!-- 底部购买和商品规格 -->
        <h3>344546</h3>
        <Standard ref="standard" :id="id" @changGg="Ggd"></Standard>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
//商品详情
import Goods from "../components/detail/DetailGoods.vue";
//
// 优品推荐
import Referral from "../components/detail/Referral.vue";
import Standard from "../components/detail/Standard.vue";
export default {
  components: {
    Goods,
    Referral,
    Standard,
  },
  data() {
    return {
      id: null,
      //   滚动导航标记
      active: 0,
      // 参数
      des: null,
      // other:null,
      g_name: null,
      // 用户评价上面的规格是否创建
      flag: false,
    };
  },
  watch: {
    $route() {
      this.getGoods();
      /*  location.reload(); */
    },
  },
  created() {
    this.getGoods();
  },
  methods: {
    //商品规格层的弹入与弹出
    showPopup() {
      // 父组件使用ref给子组件绑定，触发子组件的事件或者属性
      this.$refs.standard.showPopup();
    },
    Ggd(val) {
      // this.other=val;
      this.g_name = val;
      // console.log("99",val);
    },
    // 获取商品信息
    getGoods() {
      this.id = this.$route.params.id;
      // console.log("aaa", this.id);
      this.axios
        .get("http://47.115.51.185/api/product/detail/" + this.id)
        .then((res) => {
          let data = res.data.data;
          // console.log(data.storeInfo);
          // console.log(data);
          // 是否存在规格选项,productValue是一个数组，表示没有规格选项，是对象说明有规格选项
          if (Array.isArray(data.productValue)) {
            this.flag = false;
          } else {
            this.flag = true;
          }
          this.des = data.storeInfo.description;
        });
    },
  },
  mounted() {
    // console.log("ss0",this.$refs.standard.suks);
    // 评价区
    // let that = this;
    let top = document.querySelector(".van-sticky");
    window.onscroll = function () {
      //滚动条滚动时，距离顶部的距离
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      let t = top.offsetHeight;
      if (scrollTop > t + 100) {
        top.style.opacity = 1;
      } else {
        top.style.opacity = 0;
      }
    };
  },
  computed: {
    // pickOn(){
    //   return this.$nextTick(()=>{
    //     console.log("ss",this.$refs.standard.suks);
    //     return this.$refs.standard.suks;
    //   });
    // }
  },
  updated() {
    // console.log("ss",this.pickOn);
    // console.log(this.$refs.standard.suks);
  },
  beforeRouteUpdate(to, from, next) {
    next((vm) => {
      vm.getGoods();
    });
  },
};
</script>
<style lang="less">
#detail {
  width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #f5f5f5;
  // 顶部滚动导航条
  .van-sticky {
    transition: opacity 3s linear;
    opacity: 0;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99999;
    .van-tabs__wrap {
      .van-tabs__nav {
        // 线
        .van-tabs__line {
          bottom: 0.6rem;
          background: linear-gradient(270deg, #f36 0, #ff6533);
        }
      }
    }
  }
  // 导航内容
  .van-tabs__content {
    .praise {
      box-sizing: border-box;
      display: flex;
      align-items: center;
      font-size: 0.28rem;
      margin-top: 0.2rem;
      padding: 0.1rem 0.3rem;
      color: grey;
      .van-cell__title {
        text-align: left;
        color: #282828;
      }
      .hp {
        color: #fc4141 !important;
      }
    }
    // 商品规格左标题
    .spec {
      margin-top: 0.2rem;
      text-align: left;
      color: #82848f;
      font-size: 0.26rem;
    }
    // 商品选择
    .spec12 {
      margin-top: 0.2rem;
    }
    .products {
      margin-top: 0.2rem;
      background-color: #fff;
      width: 100vw;
      box-sizing: border-box;
      font-size: 0.3rem;
      color: #282828;
      height: 0.86rem;
      line-height: 0.86rem;
      padding: 0.1rem 0;
    }
    .param {
      background-color: #f5f5f5;
      width: 100vw;
      box-sizing: border-box;
      ul {
        text-align: left;
        overflow: hidden;
        background-color: #f5f5f5;
        li {
          background-color: #f5f5f5;
        }
      }
      div {
        width: 100vw !important;
        box-sizing: border-box !important;
        overflow: hidden;
        img {
          width: 100%;
          // height: 100%;
          height: auto;
        }
      }
    }
  }
}
// .van-tab__pane{
//     padding: 2rem 0;
// }
</style>